/* 헤더 */
@font-face {
  font-family: Arita-dotumM;
  src: url(font/Arita-dotumM.ttf);
}

@font-face {
  font-family: Arita-buriM;
  src: url(font/Arita-buriM.otf);
}
@font-face {
  font-family: Arita-buriL;
  src: url(font/Arita-buriL.otf);
}

@font-face {
  font-family: Arita-dotumSB;
  src: url(font/Arita-dotumSB.ttf);
}
/* home */
.Homes{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    margin-top: -10%;
}
.Homes img{
    width: 100%;
}

/* home end */



header{
  width: 100%;
  height: 100px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;

  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

header.active{ 
    background-color: rgba(255,255,255,0.2);
    border-bottom: none;
}
header h2{
 float: left;
 margin-left: 5%;
}
header h2 img{width: 100px;}
.nav{
 text-align: center;
 margin: 20px auto;
 float: right;
 margin-right: 10%;
 margin-top: 2%;

 font-size: 18px;
 color: #333;
 text-transform: uppercase;
 font-family: Arita-buriM;
}

.nav a{
  padding: 0 15px;
  cursor: pointer;
}

.nav a:nth-of-type(1){
    position: absolute;
    left: 5%;
    top: 35%;

    opacity: .1;
  }
  


.nav a.active{
  color:#005295;
}
main{
  height: 100%;
  background-color: #f4f4f4;
}


/* 메인 */
main div{
  margin-top: 10%;
  text-align: center;
  height: 100%;
  font-size: 14px;
  line-height: 2em;
  font-weight: bold;
}

.About .background_1{
  position: relative;
  width: 100%;  
  margin-top: -5%;
}
.About .background_1 img{
  position: relative;
  width: 100%;
}
.About .background_1 h2{
  position: absolute;
  top: 50%; left: 0%;
  width: 100%;
  font-size: 40px;
  font-weight: normal;
  line-height: 6rem;
  color: #f4f4f4;
  margin-top: -3rem;
/*   text-shadow: 4px 2px 2px rgba(0,0,0,0.1);  */
  
 font-family: Arita-buriM;
}
.About ul{
  list-style: none;
  display: flex;
  width: 60%;
  margin: 0 auto;
  cursor: pointer;
  margin-top: 8%;
  position: relative;
  background: #f4f4f4;
  font-size: 18px;
  
 font-family: Arita-dotumM;

}

.About ul li{
  padding: 2% 5%;
  float: left;
  position: relative;
  width: 50%;
  border-bottom: 2px solid #005295;
  box-sizing: border-box;

  font-weight: normal;
}

.About ul li.active{
  border-bottom: none;
  border-right: 2px solid #005295;
  border-left: 2px solid #005295;
  border-top: 2px solid #005295;
  color:#005295;
  
  font-weight: bold;
}


