/* 헤더 */
@font-face {
  font-family: Arita-dotumM;
  src: url(font/Arita-dotumM.ttf);
}

@font-face {
  font-family: Arita-buriM;
  src: url(font/Arita-buriM.otf);
}
@font-face {
  font-family: Arita-buriL;
  src: url(font/Arita-buriL.otf);
}

@font-face {
  font-family: Arita-dotumSB;
  src: url(font/Arita-dotumSB.ttf);
}



header{
  width: 100%;
  height: 100px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;

  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

header h2{
 float: left;
 margin-left: 5%;
}
header h2 img{width: 100px;}

.nav{
 text-align: center;
 margin: 20px auto;
 float: right;
 margin-right: 10%;
 margin-top: 2%;

 font-size: 18px;
 color: #333;
 text-transform: uppercase;
 font-family: Arita-buriM;
}

.nav a{
  padding: 0 15px;
  cursor: pointer;
}
.nav a:nth-of-type(1){
  position: absolute;
  left: 2%;
  top: 35%;

  
  opacity: .1;
}

.nav a:nth-of-type(1) img{
  width: 90px;
}

.nav a:nth-of-type(1) p{
  display: none;
}

.nav a.active{
  color:#005295;
}

main{
  height: 100%;
  background-color: #f4f4f4;

}


/* 메인 */
main div{
  text-align: center;
  height: 100%;
  font-size: 14px;
  line-height: 2em;
  font-weight: bold;
}

.Company .background_1{
  position: relative;
  width: 100%;
  
  margin-top: -5%;
}
.Company .background_1 img{
  position: relative;
  width: 100%;
}
.Company .background_1 h2{
  position: absolute;
  top: 50%; left: 0%;
  width: 100%;
  font-size: 40px;
  font-weight: normal;
  line-height: 6rem;
  color: #f4f4f4;
  margin-top: -3rem;
/*   text-shadow: 4px 2px 2px rgba(0,0,0,0.1);  */
  
 font-family: Arita-buriM;
}
.Company ul{
  list-style: none;
  display: flex;
  width: 70%;
  margin: 0 auto;
  cursor: pointer;
  margin-top: 8%;
  position: relative;
  background: #f4f4f4;
  font-size: 18px;
  
 font-family: Arita-dotumM;
}

.Company ul li{
  width: 80%;
  padding: 2% 5%;
  float: left;
  position: relative;
  width: 33.33%;
  border-bottom: 2px solid #005295;
  box-sizing: border-box;

  font-weight: normal;
}

.Company ul li.active{
  border-bottom: none;
  border-right: 2px solid #005295;
  border-left: 2px solid #005295;
  border-top: 2px solid #005295;
  color:#005295;
  
  font-weight: bold;
}
/* 1 */
.Company .info_1{
  width:70%;
  margin: 0 auto;
  display: flex;
  position: relative;
  padding-top: 2%;
 font-family: Arita-dotumM;

 padding-bottom: 5%; 
}
.Company .info_1 img{
  width: 50%;
}
.Company .info_1 .info_1_title{
  width: 35%;
  text-align: left;
  white-space: pre-line;
  margin-top: 20%;
  margin-left: 5%;


  line-height: 1.7;

}
.Company .info_1 .info_1_title h2{
  position: absolute;
  top: 10%;
  left: 38%;
  font-size: 5rem;
  color: #111;
  font-weight: 500;

  
 font-family: Arita-buriM;
}
.Company .info_1 .info_1_title p{  
  font-size:18px;
  font-weight: 500;
  margin-top: 50px;
}

.Company .info_1 .info_1_title .color{
  color:#005295;
}


/* 2 */
.info_2{
  padding-bottom: 15%;
}
/* 2-1 */
.info_2 .info_2-top{
  max-width: 1600px;
  margin: 0 auto;
}
.info_2 .info_2-top .info_2-top-1 {
  position: relative;
  background-size: cover;
  padding-bottom: 37.47%;
  background: url(./img/company/banner_01.jpg) center center no-repeat;
}
.info_2 .info_2-top .info_2-top-1 .info_2-top-1_font{
  position: absolute;
    left: 5%;
    top: 0%;
    right: 0;
    text-align: left;

}
.info_2 .info_2-top .info_2-top-1 .info_2-top-1_font strong{
  font-size:70px;
  color: #f4f4f4;
  font-family: Arita-buriM;
  font-weight: 500;
}
.info_2 .info_2-top .info_2-top-1 .info_2-top-1_font p{
  font-size: 16px;
  color: #fff;   
  word-break: keep-all !important;
  font-weight: normal;
  font-family:Arita-dotumM;
}
/* 2-2 */
.info_2 .info_2-mid{   
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  margin-top: -11.876%;
}
.info_2 .info_2-mid a{
  float: left;
  padding: 3.37% 0 2.949%;
  width: 33.333%;
  text-align: center;
  background: #fff;
  box-sizing: border-box;
  border:1px solid #ddd;
  color:#555;
  text-decoration: none;


  font-family: Arita-buriL;
}

.info_2 .info_2-mid a span{
  color: #d03b56;
  font-size: 16px;
}
.info_2 .info_2-mid a:nth-of-type(2) span{
  color: #19826c;
}
.info_2 .info_2-mid a:nth-of-type(3) span{
  color: #917100;
}

.info_2 .info_2-mid a p{
  margin-top: 5%;
  font-size: 34px;
  color: #555;
  line-height: 1.2em;
  font-weight: 100;
}

.info_2 .info_2-bottom{   
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  margin-top: -11.876%;

  padding-bottom: 30%;
}
.info_2 .info_2-bottom img{
  float: left;
  width: 33.333%;
  height: 350px;
  text-align: center;
  background: #fff;
  box-sizing: border-box;
  color:#555;
  text-decoration: none;
  border-right: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  font-family: Arita-buriL;
}

/* 3 */
